.mainBlock {
    position: relative;
    height: calc(100vh - 70px);
    width: 100%;
    background-color: rgba(245, 223, 78, 0.7);
    margin-top: 70px;
}

.mainBlock video {
    position: absolute;
    height: 100%;
    width: 100%;
    object-fit: cover;
    z-index: -10;
    opacity: 0.8;
}

.flexBlock {
    max-width: 1320px;
    height: 100%;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
}

.text {
    line-height: 1.3;
    width: 500px;
    font-family: 'Poppins', sans-serif;
    font-weight: 500;

    text-transform: uppercase;
}

.text h2 {
    font-size: 30px;
    font-weight: 400;
}

.text h1 {
    font-size: 40px;
}


.btnWrapper {
    height: 53px;
    display: flex;
    margin-top: 26px;
}

.btnWrapper a {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
}

.btnWrapper .btnLeft {
    color: white;
    background-color: #252b33;
    transition: all 1s;
}


.btnWrapper .btnRight {
    display: flex;
    color: #252b33;
    transition: all 1s;
}

.btnWrapper .btnRight:hover {
    color: white;
    background-color: #252b33;

}

.rounded {
    background-color: white;
    border-radius: 50%;
}

.rounded .photo {
    width: 450px;
    height: 450px;
    background-color: #252b33;
    background-position: center;
    border-radius: 50%;
    margin: 15px;
}

@media (max-width: 819px) {
    .rounded .photo {
        max-width: 350px;
        max-height: 350px;
    }
}

@media (max-width: 516px) {
    .text {
        width: 90%;

    }

    .text h1, .text h2 {
        font-size: 1.5rem;
    }

    .rounded .photo {
        max-width: 250px;
        max-height: 250px;
    }

}